define([
    'jquery'
], function($) {

    return {
        init: function() {
            $('fieldset.collapsible.clearfix').each(function() {
                if ($(this).attr('id') !== 'id_general' && $(this).attr('id') !== 'id_timing') {
                    $(this).hide();
                }
            });
        }
    };
});
